import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddSupplierReturnComponent } from './add-supplier-return/add-supplier-return.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { SupplierReturnComponent } from './supplier-return.component';
import { TranslateModule } from '@ngx-translate/core'; 
import { ScrollingModule } from '@angular/cdk/scrolling';
import { EditComponent } from './edit/edit.component';
import { BillType10Module } from 'src/app/sales/components/bill-type/bill-type-10/bill-type10.module';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: SupplierReturnComponent
      },
      {
        path: "detail-return-supplier",
        component: DetailComponent
      },
      {
        path: "add-return-supplier",
        component: AddSupplierReturnComponent,
         
      },
      {
        path: "edit-return-supplier",
        component: EditComponent,
         
      }
    ]
  }
]

@NgModule({
  declarations: [SupplierReturnComponent, AddSupplierReturnComponent, DetailComponent, EditComponent,  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ScrollingModule,
    BillType10Module
  ]
})
export class SupplierReturnModule { }
